<template>
    <div class="integral-qcode">
        <div class="content">
           <div class="content-wapper">
              <img :src="this.imgUrl" class="head-img">
              <p>{{this.ticketName}}<span class="num" v-show="this.num>1"> x{{this.num}}</span></p>
              <div class="canvas-container">
                  <canvas id="my-qcode-canvas"></canvas>
              </div>
            </div>
        </div>
    </div>
</template>

<script>
import { mapState } from "vuex";
import Vue from "vue";
import QRCode from "qrcode";

Vue.use(QRCode);
export default {
  data() {
    return {
      codes: "",
      ticketId: "",
      ticketName: "",
      imgUrl: "",
      num: 1,
      intervalId: -1000,
      leftTimes: -1
    };
  },
  created() {},
  activated() {
    if (this.intervalId != -1000) {
      window.clearInterval(this.intervalId);
      this.intervalId = -1000;
    }
    this.leftTimes = -1;
    this.intervalId = setInterval(this.intervalHandle, 2000);
    this.ticketId = this.$route.params.id;
    this.ticketName = this.$route.params.name;
    this.imgUrl = this.$route.params.imgUrl;
    this.num = this.$route.params.num;
    this.useqrcode();
    this.$store.dispatch("toggleLoading", false);
  },
  deactivated() {
    if (this.intervalId != -1000) {
      this.leftTimes = 3;
    }
  },
  methods: {
    useqrcode() {
      var canvas = document.getElementById("my-qcode-canvas");
      var codeStr =
        "fuc:integral," + "userid:" + this.userid + ",id:" + this.ticketId;

      //  var codeStr =
      //     "fuc:integral,userid:21,id:5aa3e888ab21c";

      QRCode.toCanvas(canvas, codeStr, function(error) {
        if (error) console.error(error);
        console.log("success!");
      });
    },
    intervalHandle() {
      // this.$store.dispatch("toggleLoading", true);
      this.requestHandle();
    },
    requestHandle() {
      this.$http
        .post(this.path + `php/Route.php`, {
          type: "usesourcehand",
          hand: "usesource:oid:" + this.ticketId
        })
        .then(response => {
          let data = response.body;
          if (data.toString() == "200") {
            this.scanSuccessHandle();
          } else if (this.leftTimes != -1) {
            this.leftTimes--;
            if (this.leftTimes == 0) {
              window.clearInterval(this.intervalId);
              this.intervalId = -1000;
            }
          }
        })
        .catch(response => {
          //
          console.log(response);
        });
    },
    scanSuccessHandle() {
      window.clearInterval(this.intervalId);
      this.intervalId = -1000;
      this.$store.commit("UPDATE_CONSUMEOKSHOW", true);
      // this.updatateUserInfo();
    }
  },
  computed: {
    ...mapState(["path", "icon", "gender", "userid"])
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.integral-qcode {
  background-color: #515151;
  color: #444;
  padding-top: 0.3rem;
  position: relative;

  .content {
    position: absolute;
    left: 0.25rem;
    right: 0.25rem;
    top: 0.75rem;
    bottom: 0.45rem;
    background-color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 0.04rem;

    .content-wapper {
      text-align: center;

      .head-img {
        margin-top: 0.24rem;
        width: 0.8rem;
        height: 0.8rem;
        border-radius: 0.04rem;
        border: 2px #555 solid;
        box-shadow: 0 0px 1px rgba(0, 0, 0, 0.6);
      }

      p {
        margin-top: 0.04rem;
        font-size: 0.13rem;
        letter-spacing: 0.01rem;
      }

      .canvas-container {
        margin-top: 0.12rem;
        display: inline-block;

        #my-qcode-canvas {
          width: 2rem !important;
          height: 2rem !important;
        }
      }
    }
  }
}
</style>



